export default {
	data() {
		return {
			n: true,
			show: false,
			phone: '',
			password: '',
			shade: false
		};
	},
    onLoad(){
        
    },
	methods: {
		quickLogin: function() {
			uni.redirectTo({
				url: '../otherLogin/otherLogin'
			});
		},
		forgetPassword: function() {
			uni.navigateTo({
				url: '../getBackPassword/getBackPassword',
				animationType: 'fade-in',
				animationDuration: 200
			});
		},
		next: function() {
			this.n = false;
		},
		back: function() {
			this.n = true;
		},
		cancel: function() {
			this.shade = false;
		},
		getPhoneValue: function(e) {
			this.phone = e.detail.value;
		},
		getPasswordValue: function(e) {
			this.password = e.detail.value;
		},
		sure: function() {
			uni.redirectTo({
				url: '../register/register'
			});
		},
		login() {
			var _self = this;
			if (this.phone == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入手机号'
				});
				return false;
			}
			var myreg = /^(16[0-9]|14[0-9]|13[0-9]|15[0-9]|17[0-9]|18[0-9]|19[0-9])\d{8}$$/;
			if (!myreg.test(this.phone)) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none',
					mask: true,
					duration: 2000
				});
				return false;
			}
			if (!this.password) {
				uni.showToast({
					title: '请输入密码',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			uni.request({
				url: this.url + 'users/login/',
				method: 'POST',
				data: {
					mobile: this.phone,
					password: this.password
				},
				headers: {
					'Content-Type': 'application/json'
				},
				success: res => {
					uni.setStorageSync('phone', this.phone);
					uni.setStorageSync('token', res.data.token);
					_self.global_.phone = this.phone;
					_self.global_.token = uni.getStorageSync('token');
					if (res.sthatusCode == 401) {
						this.shade = true;
					}
					if (res.statusCode == 402) {
						uni.showToast({
							title: '密码错误',
							icon: 'none'
						});
					}
					if (res.statusCode == 200) {
						uni.reLaunch({
							url: '../index/index'
						});
					}
					if (res.statusCode == 412) {
						uni.showToast({
							title: '请用手机短信快速登录,并设置密码',
							icon: 'none'
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
};
